@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    font-family: sans-serif;
    text-align: center;
  }
  
*,
*:before,
*:after {
  box-sizing: border-box;
}
body {
  padding: 0px;
  margin: 0px;
  background: #f4f1f1;
  font-family: "Open Sans", sans-serif;
  color: black;
  font-size: 15px;
}

div#bmrcalc {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}
.form {
  width: 600px;
  background: white;
  padding: 20px;
  border: 1px solid #ffc5c5;
  box-shadow: 0 1px 9px #cdb3b3;
  border-radius: 5px;
  max-width: 100%;
}
h2 {
  margin: 0px;
  text-align: center;
  font-size: 21px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.inputwrap {
  display: block;
  width: 100%;
  margin-bottom: 10px;
  text-align: left;
}

.inputwrap label {
  display: block;
  font-size: 16px;
  margin-bottom: 4px;
}

input[type="text"],
input[type="number"] {
  padding: 8px 7px;
  width: 110px;
  margin-right: 10px;

  font-family: "Open Sans", sans-serif;
  font-size: 15px;
}
input[type="text"] {
  width: 100%;
}

input[type="submit"] {
  width: 100%;
  display: block;
  background: black;
  color: white;
  text-transform: uppercase;
  padding: 10px 0px;
  border: none;

  font-family: "Open Sans", sans-serif;
  font-size: 15px;
}
.btn {
  display: block;
  width: 100%;
  padding: 10px 1px;
  background: black;
  color: white;
  border: none;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 16px;
}
select {
  padding: 10px 10px;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
}
.error {
  background: #bf2121;
  color: white;
  text-align: center;
  padding: 4px 16px;
  margin-bottom: 15px;
  display: inline-block;
}
.result {
  background: red;
  color: white;
  text-align: center;
  padding: 10px 0px;
  margin-top: 9px;
  font-size: 18px;
}
.workout {
  margin-top: 19px;
}
button:hover {
  opacity: 0.8;
}
.hidden {
  display: none;
}
@media only screen and (max-width: 500px) {
  div#bmrcalc {
    align-items: end;
  }
}

